<template>
    <div class="midata">
         <div class="box">
           <div class="outgo-box" @click="goPrenext">
                <van-icon  name="arrow-left" size="30" /><span class="outgo">返回</span>
           </div>
           <div class="mi-cont" @click="preservation">
               <span>保存</span>
           </div>
        </div>
         <div class="personalData">

            <div class="headPortrait">
                <img :src="imgStr" alt="" @click="portraitText">
            </div>

            <div class="data-img">
                <span>请上传头像</span>
                <input id="inputer" type="file"  name="img" accept="image/*" @change="onchangeImgFun" >
            </div>
        </div>
        <div class="tit">
            <h2>基本资料</h2>
        </div>
        <div>
            <van-cell-group>
                <van-field v-model="value" label="昵称" placeholder="请输入昵称" />
            </van-cell-group>
        </div>
        <div>
            <van-field name="radio" label="性别">
                <template #input>
                    <van-radio-group v-model="radio" direction="horizontal" @change="sex(radio)">
                    <van-radio name="1" >{{man}}</van-radio>
                    <van-radio name="2">{{woman}}</van-radio>
                    </van-radio-group>
                </template>
            </van-field>
        </div>
        <div>
            <van-cell-group>
                <van-field v-model="value2" label="爱好" placeholder="请输入 兴趣丶爱好" />
            </van-cell-group>
        </div>
    </div>
</template>

<script>

    export default {
        
        name:'midata',
        data(){
            return{
                imgStr: require('../assets/rto.png'),
                errorStr:'',
                miDate:{
                    cot:'保存'
                },
              value:'',
              value2:'',
              radio:'1',
              man:'男',
              woman:'女',
              proText:{},
              myImg:'',
              imgArr:[],
              pre:{},
            }
        },
        
        created(){
            let str = JSON.parse(localStorage.getItem('str')) || [];
            //  console.log(str);
            if(str.length >= 1){
                // console.log(str[str.length-1]);
                this.imgStr = str[str.length-1].url
            }
        },
        methods:{
             // 返回上一级
            goPrenext(){
                this.$router.go(-1);
            },
            // 保存资料
            preservation(){
                // console.log('123');
                if(this.value == '' || this.value2 == ''){
                    this.$toast('请填写完成资料信息');
                    return;
                }
                localStorage.setItem('str',JSON.stringify(this.imgArr))
                this.proText.sex = this.radio == '1' ? this.man : this.woman
                this.pre = {
                    prename:this.value,
                    presex:this.proText.sex,
                    preinterest:this.value2,
                }
                localStorage.setItem('preser',JSON.stringify(this.pre))
                this.$router.go(-1);
                // console.log(this.value,this.value2,this.proText.sex);
                
            },
             // 监听上传头像input框的事件
            onchangeImgFun(e){
                // console.log(e.target.files[0]);
                let file = e.target.files[0]
                let imagSize = file.size
                // 改变this指向
                let _that = this
                // console.log(_that);
                if(imagSize <= 50 * 1024){
                    // _that.errorStr = '大小合适'
                    _that.errorStr = '大小合适'
                    // console.log(_that.errorStr,_that.errorStr);
                    // console.log(_that.errorStr);
                    let reader = new FileReader()
                    reader.readAsDataURL(file)
                    // console.log(reader);
                    reader.onloadend = function(){
                        let dataURL = reader.result
                        // require(dataURL)
                        _that.imgStr = dataURL
                        // console.log(dataURL);
                        // let res = _that.imgStr
                        let imgres = {
                            url:dataURL
                        };

                        _that.imgArr.push(imgres)
                        // console.log(_that.imgArr);

                        // let atr = JSON.stringify(imgArr)
                       

                    }
                }else{
                    // _that.errorStr = '大小不合适'
                    _that.errorStr = '大小不合适'
                    // console.log('大小不合适');
                    // console.log(_that.errorStr);
                }
            },
            // 点击头像触发上传头像
            portraitText(){
                let inputer = document.getElementById('inputer')
                inputer.click();
                // console.log('123');
            },
            sex(e){
                // console.log(e);
                this.proText.sex = e === '1' ? this.man : this.woman
                // console.log(this.proText);
            },

        },
        mounted(){
            this.sex()
            // console.log(this.radio);

            
        }

    }
</script>

<style lang="less" scoped>
 .data-img{
        width: 60px;
        height: 16px;
        margin: 0 auto 30px;
        overflow: hidden;
        input{
            display: inline-block;
           
            width: 100%;
        }
    }
    .tit{
        margin-left: 10px;
    }
     .headPortrait{
        width: 80px;
        height: 80px;
        margin: 0 auto 10px;
        img{
            display: inline-block;
            width: 100%;
            height: 100%;
            border-radius: 50%;
        }
    }
 .box{
        padding: 10px 0 0 0px;
        display: flex;
        justify-content: space-between;
        .outgo-box{
            position: relative;
        .outgo{
            display: inline-block;
            width: 50px;
            font-size: 24px;
            position: absolute;
            top: -2px;
            }
        }
        .mi-cont{
            margin-right: 10px;
            span{
                display: inline-block;
                font-size: 16px;
            }
        }

    }
</style>